// todo 手机号验证
var options={
    elem:$("#phone_num"),
    msg:"手机号",
    elemTip:$(".code_error")
}
validityForm(options);
function validityForm(options) {
    options.elem[0].onblur=function () {
        if(options.elem[0].validity.valid){
            options.elemTip.html("");
        }
        if(options.elem[0].validity.valueMissing){
            options.elemTip.html(options.msg+"不能为空");
        }
        if(options.elem[0].validity.patternMismatch){
            options.elemTip.html(options.msg+"格式不正确");
        }
    }
}
$("#get_code").click(function () {
    var phone=$("#phone_num").val();
    if(phone!="") {
        showDialog(2);
        $.post(sendmsgurl,{phone:phone},function (result) {
            if(parseInt(result.status)==0){
                showDialog(0,"验证码已发送");
                settime($("#get_code"));
            }else {
                showDialog(1,"验证码发送失败",2);
            }
        },"json")
    }
})
var countdown=60;
function settime(obj) {
    // console.log(obj);
    if (countdown == 0) {
        console.log(obj);
         obj[0].removeAttribute("disabled");
         obj.html("获取验证码");
        countdown = 60;
        return;
    } else {
         obj[0].setAttribute("disabled", true);
         obj.html("重新发送(" + countdown + "s)");
        countdown--;
    }
    setTimeout(function() {
            settime(obj) }
        ,1000)
}


