'use strict';
function PatternCheck() {

}

PatternCheck.prototype = {
    isPhone: function (s) {
        var patrn = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
        return patrn.exec(s);
    },
    isTrueName: function (s) {
        var patrn =  /^[\u4e00-\u9fa5 ]{2,20}$/;
        return patrn.exec(s);
    },
    isBankNum: function (s) {
        var patrn = /^\d{16}|\d{19}$/;
        return patrn.exec(s);
    },
    isCheckCode: function (s) {
        var patrn = /^\d{6}$/;
        return patrn.exec(s);
    },
    isBankName: function (s) {
        var patrn = /^[\u4e00-\u9fa5 ]{4,20}$/;
        return patrn.exec(s);
    },
    isSimplePwd: function (s) {
        return /^\d{6}$/.exec(s);
    }
};


