$("#get_code").click(function () {
    var phone=$("#phone_num").val();
    if(phone!="") {
        showDialog(2);
        $.post(checkmobileurl, {phone: phone}, function (result) {
            if (parseInt(result.error_code) == 0) {
                $.post(sendmsgurl, {phone: phone}, function (result) {
                    if (parseInt(result.error_code) == 4113) {
                        var codeError = result.error_info;
                        showDialog(1, codeError, 2);
                    } else {
                        showDialog(0, "发送成功");
                        settime($("#get_code"));
                    }
                })
            }
            else {
                var phoneError = result.error_info;
                showDialog(1, phoneError, 2);
            }
        }, "json")
    }
})
$("#confirm_btn").click(function () {
    var phone=$("#phone_num").val();
    var imageCode= $("#image_code").val();
    if(phone!=""&&imageCode!="") {
        $.post(checkcodeurl, {image_code: imageCode}, function (result) {
            if (parseInt(result.error_code) == 0) {
                showDialog(0);
                $(".code_error").hide();
                var openId = $("#open_id").val();
                window.location.href = result.error_info + "&phone=" + phone + "&open_id=" + openId;
            } else {
                var checkcodeError = result.error_info;
                $(".code_error").html(checkcodeError);
            }
        }, "json")
    }
});

var countdown=60;
function settime(obj) {
    // console.log(obj);
    if (countdown == 0) {
        console.log(obj);
         obj[0].removeAttribute("disabled");
         obj.html("获取验证码");
        countdown = 60;
        return;
    } else {
         obj[0].setAttribute("disabled", true);
         obj.html("重新发送(" + countdown + "s)");
        countdown--;
    }
    setTimeout(function() {
            settime(obj) }
        ,1000)
}


