$("#get_code").click(function () {
    var phone=$("#phone_num").val();
    if(phone!=""){
        showDialog(2);
        $.post(sendmsgurl, {user_cellphone: phone}, function (result) {
            if(parseInt(result.error_code)==0){
                showDialog(0,"短信已发送");
            }else {
                showDialog(1,result.error_info)
            }
        },"json")
    }else {
        $(".code_error").html("手机号不能为空");
    }
})
$("#confirm_btn").click(function () {
    var phone=$("#phone_num").val();
    var imageCode= $("#image_code").val();
    if(phone!=""&&imageCode!=""){
        $.post(checkcodeurl, {user_cellphone: phone,msg_code:imageCode}, function (result) {
            if (parseInt(result.error_code) == 0) {
                showDialog(0);
                $(".code_error").hide();
                window.location.href = indexurl;
            } else {
                var checkcodeError = result.error_info;
                $(".code_error").html(checkcodeError);
            }
        },"json")
    }else {
        $(".code_error").html("手机号或验证码不能为空");
    }
});

var countdown=60;
function settime(obj) {
    // console.log(obj);
    if (countdown == 0) {
        console.log(obj);
         obj[0].removeAttribute("disabled");
         obj.html("获取验证码");
        countdown = 60;
        return;
    } else {
         obj[0].setAttribute("disabled", true);
         obj.html("重新发送(" + countdown + "s)");
        countdown--;
    }
    setTimeout(function() {
            settime(obj) }
        ,1000)
}



