var classStatus = true;
var singleStatus = true;
var classPage = 1;
var singlePage=1;
$(function() {
    if (window.location.hash == "#class") {
        console.log("切换到班课");
        $("#class_btn").click();
    } else if (window.location.hash == "#one_to_one") {
        console.log("切换到一对一");
        $("#one_btn").click();
    } else {
        console.log("页面加载触发", classStatus);
        classStatus = false;
        singleStatus = false;
        getClass();
    }
});
$("#class_btn").click(function() {
    window.location.hash = "#class";
    singleStatus = false;
    classStatus = true;
    if ($("#class .list-group-item").length < 1) {
        console.log("点击触发班课", classStatus);
        classStatus= false;
        getClass();
    }
});
$("#one_btn").click(function() {
    // console.log("click");
    window.location.hash = "#one_to_one";
    classStatus = false;
    singleStatus = true;
    if ($("#one_to_one .list-group-item").length < 1) {
        console.log("点击触发一对一", singleStatus);
        singleStatus = false;
        getSingle();
    }
});
function getClass() {
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(classurl, {
        p: classPage
    }, function(result) {
        if(parseInt(result.error_code) == 0){
        result = result.error_info;
        var html = "";
        $.each(result, function(i, class_comment) {
            html += '<div class="list-group-item"><div class="row"><div class="col-xs-7"><p class="course_name over-text">'+class_comment.goods_name+'</p>';
            html += '<p class="teacher_name">上课老师：<span>'+class_comment.teacher_name+'</span></p>';
            var time = class_comment.class_time;
            html += '<p class="course_time">上课时间：';
            for (var i = 0; i < time.length; i++) {
                    html += '<span>'+time[i]+'</span>'
            }
            html += '</p></div><div class="col-xs-5"><ul class="grade">';
            var star = Math.floor(class_comment.geval_t_point);
            for (var i = 0; i < star; i++) {
            html += '<li class="active"></li>';
            }
            for (var i = 0; i < 5 - star; i++) {
            html += '<li></li>';
            }
            html += '</ul><p>综合得分：<span>'+(class_comment.geval_t_point==null?0:class_comment.geval_t_point)+'</span>分</p></div></div></div>';
        })
        classPage++;

        $("#class .list-group").html(html);
        if (result.length < 10) {
            $(".loader").html('<img src="'+imgurl+'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
    }else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        classStatus = true;
    }, "json")
}
function getSingle() {
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(singleurl, {
        p: singlePage
    }, function(result) {
        if(parseInt(result.error_code) == 0){
            result = result.error_info;
            var html = "";
            $.each(result, function(i, class_comment) {
                html += '<div class="list-group-item"><div class="row"><div class="col-xs-7"><p class="course_name over-text">'+class_comment.goods_name+'</p>';
                html += '<p class="teacher_name">上课老师：<span>'+class_comment.teacher_name+'</span></p>';
                html += '</div><div class="col-xs-5"><ul class="grade">';
                var star = Math.floor(class_comment.geval_t_point);
                for (var i = 0; i < star; i++) {
                    html += '<li class="active"></li>';
                }
                for (var i = 0; i < 5 - star; i++) {
                    html += '<li></li>';
                }
                html += '</ul><p>综合得分：<span>'+(class_comment.geval_t_point==null?0:class_comment.geval_t_point)+'</span>分</p></div></div></div>';
            })
            singlePage++;
            $("#one_to_one .list-group").html(html);
            if (result.length <= 10) {
                $(".loader").html('<img src="'+imgurl+'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
            }
        }else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        singleStatus = true;
    }, "json")
}
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h && $("body").height() > h) {
        if (classStatus) {
            console.log("滚动class", classStatus)
            classStatus = false;
            getClass();
        } else if (singleStatus) {
            console.log(newH, h, $("body").height(), "滚动one_to_one", singleStatus)
            singleStatus = false;
            getSingle();
        }
    }
});


