$(".share_img").change(function() {
    new html5ImgCompress(event.target.files[0], {
        maxWidth: 200,
        maxHeight: 200,
        quality: 0.6,
        before: function(file) {
            console.log('压缩前...');
            // 这里一般是对file进行filter，例如用file.type.indexOf('image') > -1来检验是否是图片
            // 如果为非图片，则return false放弃压缩（不执行后续done、fail、complete），并相应提示
        },
        done: function(file, base64) {
            console.log('压缩成功...');
            // ajax和服务器通信上传base64图片等操作
            var size = (base64.length / 1333).toFixed(2);
            document.querySelector(".img-responsive").src = base64;
            console.log(size);
        },
        fail: function(file) {
            console.log('压缩失败...');
        },
        complete: function(file) {
            console.log('压缩完成...');
        },
        notSupport: function(file) {
            console.log('浏览器不支持！')
            // 不支持操作，例如PC在这里可以采用swfupload上传
        }
    });
});
$(document).on("click", ".btn-group button", function() {
    var $this = $(this);
    $.post(sendUrl, {
        "template_logo": document.querySelector(".img-responsive").src,
        "template_title": $("#title").val(),
        "template_desc": $("#desc").val(),
        "id": id
    }, function(r) {
        if (r.status == "0") {
            if ($this.index() == 1) {
                location.href = r.data;
            } else {
                showDialog(0, "保存成功");
            }
        } else {
            showDialog(1, "保存失败，请重试");
        }
    }, "json");
});


