var page = 1;
/*
 签到
 */
$(document).on('click', '.sign_btn', function() {
    // showDialog(2);
    var send = {},
        studentObj = {},
        studentArray = [];
    send.class_id = classid;
    studentArray.push({
        student_id: studentid,
        vendor_member_id: memberid,
        sign_type: 1
    });
    send.student_data = studentArray;
    var _this = $(this).parent();
    $.post(signurl, send, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            $(".sign-backed").parent().html('');
            _this.html('<a class="btn btn-default btn-xs sign-backed" href="javascript:">撤回</a>');
            _this.prev().html('<span>已签到</span><p>' + result.sign_time + '</p>');
            _this.parent().parent().parent().attr("data-id", result.sign_id);
            var data_num = $(".sign-history-list li").length + 1;
            $(".sign-history-list").prepend('<li class="list-group-item"><div class="container-fluid"><div class="row"><div class="col-xs-3 text-center num">第' + data_num + '次课</div> <div class="col-xs-6"> </div> <div class="col-xs-3 text-right"><a class="btn btn-primary btn-xs sign_btn" href="javascript:">签到</a></div> </div> </div> </li>');
            showDialog(0);
        } else if (parseInt(result.status) == 2) {
            showDialog(1, "请勿频繁操作，请稍后再试！");
        } else {
            showDialog(1, "请稍后再试！");
        }
    });
});
/*撤销签到*/
$(document).on("click", ".sign-backed", function() {
    $("#sign-backed-dialog").modal({});
    signStatus = false;
});
$("#sign-backed-confirm").click(function() {
    $.post(signbackurl, {
        sign_id: $(".sign-history-list li:nth-child(2)").attr("data-id")
    }, function(result) {
        if (parseInt(result.status) == 0) {
            $(".sign-history-list li:first").remove();
            $(".sign-backed").parent().prev().html("");
            $(".sign-backed").parent().html('<a class="btn btn-primary btn-xs sign_btn" href="javascript:">签到</a>');
            $(".sign-history-list li:nth-child(2)").find(".text-right").html('<a class="btn btn-default btn-xs sign-backed" href="javascript:">撤回</a>');
            showDialog(0, "撤销成功");
        } else {
            hideDialog(1, "撤销失败")
        }
        $("#sign-backed-dialog").modal('hide');
    }, 'json');
});
$('#photo input').on('change', function(e) {
    new html5ImgCompress(e.target.files[0], {
        maxWidth: 600,
        maxHeight: 600,
        quality: 0.6,
        before: function(file) {
            console.log('压缩前...');
            // 这里一般是对file进行filter，例如用file.type.indexOf('image') > -1来检验是否是图片
            // 如果为非图片，则return false放弃压缩（不执行后续done、fail、complete），并相应提示
        },
        done: function(file, base64) {
            console.log('压缩成功...');
            // ajax和服务器通信上传base64图片等操作
            img = new Image()
            var size = (base64.length / 1333).toFixed(2);
            img.src = base64;
            img.onload = function() {
                $("#photo").prepend(img)
                $("#del-photo-btn").show();
            }
            console.log(size);
        },
        fail: function(file) {
            console.log('压缩失败...');
        },
        complete: function(file) {
            console.log('压缩完成...');
            $("#loading").modal('hide');
        },
        notSupport: function(file) {
            console.log('浏览器不支持！')
                // 不支持操作，例如PC在这里可以采用swfupload上传
        }
    });
});
$(document).on("click", "img", function(e) {
    e.preventDefault();
    var imgs = $("#comment img");
    for (var i = 0, urls = []; i < imgs.length; i++) {
        urls.push(imgs[i].src);
    }
    wx.previewImage({
        current: e.target.src,
        urls: urls
    });
});
$("#del-photo-btn").click(function() {
    $(this).hide();
    $("#photo img").remove();
});
$("#comment-text").keyup(function() {
    var txt = $.trim($(this).val());
    var txtL = txt.length;
    if (txtL > 300) {
        showDialog(1, "点评内容限制300字以内！", 2)
        $(this).val(txt.substr(0, 300));
    };
});
$("#comment-btn").click(function() {
    var cont = $.trim($("#comment-text").val());
    if (cont.length < 2) {
        showDialog(1, "点评内容至少填写2个字", 2);
        return false;
    };
    var send = {};
    send.class_id = classid;
    send.content = $.trim($("#comment-text").val());
    send.file = $("#photo img").attr("src");
    showDialog(2);
    $.post(commenturl, send, function(result) {
        if (parseInt(result.status) == 0) {
            location.reload();
            showDialog(0);
        } else {
            showDialog(1);
        };
    });
});
// todo 获取历史评价
var commentStatus = true;
var signStatus = true;
$(function() {
    if (window.location.hash == "#sign") {
        console.log("切换到课堂签到");
        $("#sign_btn").click();
    } else if (window.location.hash == "#comment") {
        console.log("切换到课堂点评");
        $("#history_btn").click();
    } else {
        console.log("页面加载触发", signStatus);
        commentStatus = false;
        signStatus = false;
        getsignhistory();
    }
});
$("#history_btn").click(function() {
    console.log("click")
    window.location.hash = "#comment";
    signStatus = false;
    commentStatus = true;
    if ($("#history_comment a").length < 1) {
        console.log("点击触发历史", commentStatus);
        commentStatus = false;
        getcommenthistory();
    }
});
$("#sign_btn").click(function() {
    window.location.hash = "#sign";
    commentStatus = false;
    signStatus = true;
    if ($(".sign-history-list li").length < 1) {
        console.log("点击触发", signStatus);
        signStatus = false;
        getsignhistory();
    }
});

function getcommenthistory() {
    console.log("去获取comment");
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(historyurl, {
        class_id: classid,
        p: page
    }, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            var html = "";
            $.each(result, function(i, history) {
                html += '<a class="list-group-item" href="'
                html += commentinfo + '&eval_id=' + history.geval_deta_id
                html += '"><div class="container-fluid"><div class="row comments-head"><div         class="col-xs-6">';
                html += history.geval_deta_time
                html += '</div><div class="col-xs-6 text-right"><span class="edit-bg">家长反馈</span>';
                if (history.read > 0) {
                    html += '<span class="comments-num">' + history.eval_num + '条</span>';
                } else {
                    html += '<span>' + history.eval_num + '条</span>';
                };
                html += '</div></div><div class="row comments-body"><div class="col-xs-12">' + history.geval_deta_text + '</div>';
                if (history.pic != null && history.pic.wxh5_pic != "") {
                    html += '<div class="col-xs-12"><img src="' + history.pic.wxh5_pic + '" alt=""></div>';
                };
                html += '</div></div></a>';
            });
            $("#history_comment").append(html);
            page++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多历史记录');
            }
        } else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        commentStatus = true;
    }, 'json')
};
// todo 历史签到
var signhistoryPage = 1;

function getsignhistory() {
    console.log("去获取sign");
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(historysignurl, {
        class_id: classid,
        p: signhistoryPage
    }, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            var html = "";
            $.each(result, function(i, history) {
                html += '<li class="list-group-item" data-id="';
                html += history.id;
                html += '"><div class="container-fluid"><div class="row"><div class="col-xs-3 text-center num" data-num="';
                html += history.num;
                html += '">第';
                html += history.num;
                html += '次课</div><div class="col-xs-6"><span>已签到</span><p>';
                html += history.sign_time;
                html += '</p></div><div class="col-xs-3 text-right">';
                html += '</div></div></div></li>';
            });
            $(".sign-history-list").append(html);
            $(".sign-history-list   li:first .text-right").html('<a class="btn btn-default btn-xs sign-backed" href="javascript:">撤回</a>');
            var data_num = $(".sign-history-list li").length + 1;
            $(".sign-history-list").prepend('<li class="list-group-item"><div class="container-fluid"><div class="row"><div class="col-xs-3 text-center num">第' + data_num + '次课</div> <div class="col-xs-6"> </div> <div class="col-xs-3 text-right"><a class="btn btn-primary btn-xs sign_btn" href="javascript:">签到</a></div> </div> </div> </li>');
            signhistoryPage++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多历史记录');
            }
        } else {
            $(".sign-history-list").prepend('<li class="list-group-item"><div class="container-fluid"><div class="row"><div class="col-xs-3 text-center num">第1次课</div> <div class="col-xs-6"> </div> <div class="col-xs-3 text-right"><a class="btn btn-primary btn-xs sign_btn" href="#" >签到</a></div> </div> </div> </li>');
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史记录');
        }
        signStatus = true;
    }, 'json')
}
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h && $("body").height() > h) {
        if (commentStatus) {
            console.log("滚动comment", commentStatus)
            commentStatus = false;
            getcommenthistory();
        } else if (signStatus) {
            console.log(newH, h, $("body").height(), "滚动sign", signStatus)
            signStatus = false;
            getsignhistory();
        }
    }
});


