"use strict"
/**
 * [获取多级地址]
 * @param  {[string/num]} num [上级地址编码]
 * @param  {[string]} obj [下级地址的元素id]
 * @return {[type]}     [description]
 */
function getArea(num, obj) {
    switch (obj) {
        case "#vendor_prov_code":
            $("#vendor_prov_code,#vendor_city_code,#vendor_dist_code,#vendor_stre_code").html("<option value='0'>—请选择—</option>");
            break;
        case "#vendor_city_code":
            $("#vendor_city_code,#vendor_dist_code,#vendor_stre_code").html("<option value='0'>—请选择—</option>");
            break;
        case "#vendor_dist_code":
            $("#vendor_dist_code,#vendor_stre_code").html("<option value='0'>—请选择—</option>");
            break;
        case "#vendor_stre_code":
            $("#vendor_stre_code").html("<option value='0'>—请选择—</option>");
            break;
        default:
            console.info("getArea()输入有误！" + obj);
    };
    $.ajax({
        url: areaUrl,
        type: "GET",
        data: "area_f_code=" + num,
        dataType: "json",
        beforeSend: function() {
            showDialog(2);
        },
        success: function(request) {
            if (request.length != 0) {
                var tpl = "";
                for (var j = 0; j < request.length; j++) {
                    tpl += "<option value ='" + request[j].area_code + "'>" + request[j].area_name + "</option>";
                };
                $(obj).append(tpl);
            } else {
                $(obj).html("<option value ='1'>无</option>");
            }
            hideDialog();
        },
        error: function(XMLHttpRequest, textStatus, errorThrown) {
            showDialog(1, "请求服务器失败");
            console.log(XMLHttpRequest + "\n" + textStatus + "\n" + errorThrown);
        }
    });
};
/**
 * [显示正确input]
 * @param {[object]} el [description]
 */
function inputSuccess(el) {
    el.parents(".form-group").removeClass("has-error").addClass("has-success");
};
/**
 * [所有错误加红框]
 * @param  {[jquery object]} el  [jq对象]
 * @param  {[string]} txt [错误信息]
 * @return {[type]}     [description]
 */
function showFirstError(el, txt) {
    console.log(canNotPass, isFirstShow, txt)
    el.parents(".form-group").removeClass("has-success").addClass("has-error");
    //仅提示第一个错误
    if (isFirstShow) {
        showDialog(1, txt);
        isFirstShow = false;
        el.focus();
    };
    canNotPass = true;
}
var timer = 30,
    timeoutID,
    isFirstShow = true,
    canNotPass = true;

function countDown() {
    setTimeout(function() {
        if (timer > 0) {
            $("#sendcode").text("重新发送（" + timer + "）").attr("disabled", true);
            timer--;
            timeoutID = countDown();
        } else {
            clearTimeout(timeoutID);
            timer = 90;
            $("#sendcode").text("获取验证码").attr("disabled", false);
        }
    }, 1000);
}
var telRuleCheck = function(string) {
    var pattern = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/,
        status = false;
    if (pattern.test(string)) {
        $.ajax({
            url: checkMobile,
            type: "post",
            data: "mobile=" + string,
            dataType: "json",
            async: false,
            success: function(data) {
                if (data.check == 0) {
                    status = true;
                } else {
                    showFirstError($("#mobile"), data.text);
                    canNotPass = true;
                    isFirstShow = true;
                };
            }
        });
    }
    return status;
};
var passwordLevel = function(pass) {
    if (pass.length < 8) {
        return 0;
    }
    var ls = 0;
    if (pass.match(/([a-z])+/)) {
        ls++;
    }
    if (pass.match(/([0-9])+/)) {
        ls++;
    }
    if (pass.match(/[^a-zA-Z0-9]+/)) {
        ls++;
    }
    return ls;
};
$(function() {
    $(document).on("click", "#next-setp", function() {
        //验证必填项
        $("#info input,#info select").each(function(i, el) {
            var $el = $(el);
            if (el.nodeName == "INPUT") {
                if ($el.attr("required") !== undefined) {
                    if ($.trim($el.val()) == "") {
                        showFirstError($el, $(el).attr("placeholder"));
                        canNotPass = true;
                    } else {
                        inputSuccess($el);
                        canNotPass = false;
                    }
                }
            } else {
                if ($(el).val() == "0") {
                    showFirstError($el, "请选择" + $(el).prev().text().substring(0, $(el).prev().text().length - 1));
                } else {
                    inputSuccess($el);
                    canNotPass = false;
                }
            }
        });
        //手机号验证
        var tel = $("#mobile").val();
        if (tel == "") {
            showFirstError($("#mobile"), $("#mobile").attr("placeholder"));
        } else {
            if (!telRuleCheck(tel)) showFirstError($("#mobile"), "请输入正确的手机号码");
        };
        //验证码位数
        if ($("#sms_code_txt").val().length != 6) {
            showFirstError($("#sms_code_txt"), "请输入正确的验证码");
        }
        //密码强度
        if (passwordLevel($("#pw1").val()) < 2) {
            showFirstError($("#pw1"), "密码长度8-16位，数字、字母、字符至少包含两种");
        }
        //重复密码验证
        if ($("#pw1").val() !== $("#pw2").val()) {
            showFirstError($("#pw2"), "确认登录密码错误");
        }
        if (!canNotPass) {
            $(".step div").eq(1).addClass("hover");
            $("#info").slideUp();
            $("#pic").slideDown();
        }
        isFirstShow = true;
    });
    $(document).on("click", "#sendcode", function() {
        var tel = $("#mobile").val();
        if (telRuleCheck(tel)) {
            $.ajax({
                url: msgUrl,
                type: "POST",
                data: "mobile=" + tel,
                dataType: "json",
                success: function(request) {
                    var code = request.check;
                    if (code != undefined) {
                        switch (code) {
                            case "0":
                                showDialog(0, "发送成功");
                                countDown();
                                break;
                            default:
                                showDialog(1, request.text);
                        };
                        $("#sms_code_txt").focus();
                    } else {
                        showDialog(1, "未返回任何数据");
                    };
                },
                error: function(XMLHttpRequest, textStatus, errorThrown) {
                    showDialog(1, "请求服务器失败");
                    console.log(XMLHttpRequest + "\n" + textStatus + "\n" + errorThrown);
                }
            });
        };
    });
    $(document).on("click", "#prev-setp", function() {
        $(".step div").eq(1).removeClass("hover");
        $("#pic").slideUp();
        $("#info").slideDown();
    });
    $("#pic_type").change(function() {
        if ($(this).val() == "3") {
            $(".idcard").slideDown();
        } else {
            $(".idcard").slideUp();
        };
    });
    $(document).on("change", "#image,#image_1", function() {
        //预览图片ID
        var cacheImg = new Image(),
            $el = $(this).parent(),
            //压缩后的图片宽
            imgMaxWidth = 500,
            //压缩后的图片高，若未设置则等宽
            imgMaxHeight = 1000;
        new html5ImgCompress(event.target.files[0], {
            maxWidth: imgMaxWidth,
            maxHeight: imgMaxHeight,
            quality: 0.6,
            before: function(file) {
                showDialog(2);
                console.log('压缩前...');
                // 这里一般是对file进行filter，例如用file.type.indexOf('image') > -1来检验是否是图片
                // 如果为非图片，则return false放弃压缩（不执行后续done、fail、complete），并相应提示
            },
            done: function(file, base64) {
                console.log('压缩成功...');
                // ajax和服务器通信上传base64图片等操作
                cacheImg.src = base64;
                $el.append(cacheImg);
                hideDialog();
                console.log((base64.length / 1333).toFixed(2));
            },
            fail: function(file) {
                console.log('压缩失败...');
            },
            complete: function(file) {
                console.log('压缩完成...');
            },
            notSupport: function(file) {
                console.log('浏览器不支持！')
                // 不支持操作，例如PC在这里可以采用swfupload上传
            }
        });
    });
    $(document).on("click", "#signup", function() {
        if (image.value == "") {
            showDialog(1, "请上传证件照片");
            return false;
        } else if (image_1.value == "") {
            showDialog(1, "请上传门店照片");
            return false;
        }
        $.ajax({
            url: singUpUrl,
            type: "POST",
            data: {
                "name_vendor": $("#name_vendor").val(),
                "vendor_prov_code": $("#vendor_prov_code").val(),
                "vendor_city_code": $("#vendor_city_code").val(),
                "vendor_dist_code": $("#vendor_dist_code").val(),
                "vendor_stre_code": $("#vendor_stre_code").val(),
                "vendor_prov_name": $("#vendor_prov_code :selected").text(),
                "vendor_city_name": $("#vendor_city_code :selected").text(),
                "vendor_dist_name": $("#vendor_dist_code :selected").text(),
                "vendor_stre_name": $("#vendor_stre_code :selected").text(),
                "vendor_addr": $("#vendor_addr").val(),
                "vendor_c_name": $("#vendor_c_name").val(),
                "mobile": $("#mobile").val(),
                "code": $("#sms_code_txt").val(),
                "pw": $("#pw1").val(),
                "log_invite_code": $("#log_invite_code").val(),
                "pic_type": $("#pic_type").val(),
                "image": $(".uploadimg img")[0].src,
                "image_1": $(".uploadimg img")[1].src
            },
            dataType: "json",
            success: function(request) {
                if (request.status == "0") {
                    $(".step div").eq(2).addClass("hover");
                    $("#pic").slideUp();
                    $("#done").slideDown();
                } else {
                    if (request.status == "10001") {
                        $("#prev-setp").click();
                        $("#sms_code_txt").parents(".form-group").removeClass("has-success").addClass("has-error");
                        scrollTo(0,500);
                    };
                    showDialog(1, request.data);
                }
            },
            error: function(XMLHttpRequest, textStatus, errorThrown) {
                showDialog(1, "请求服务器失败");
                console.log(XMLHttpRequest + "\n" + textStatus + "\n" + errorThrown);
            }
        });
    });
    getArea(0, "#vendor_prov_code");
});


