"use strict"
$(function() {
    getOrderList();
    $('#date_chk').mobiscroll().date({
        theme: 'mobiscroll',
        lang: 'zh',
        display: 'modal',
        dateFormat: 'yyyy-mm-dd',
        maxDate: new Date(),
        minDate: new Date(1477958400000),
        multiline: 2
    });
    $(document).on("change", "#date_chk", function() {
        $("#date_show").text($(this).val());
        $("#show_all").show();
        page = 1;
        $("#order_list").empty();
        getOrderList();
    });
    $(document).on("click", "#show_all", function() {
        $(this).hide();
        $("#date_show").text("全部日期").find("input").val("");
        page = 1;
        $("#order_list").empty();
        getOrderList();
    });
    var timer;
    $(window).scroll(function() {
        clearTimeout(timer)
        timer = setTimeout(function() {
            if (window.scrollY > $(document).height() - window.screen.availHeight - 200) {
                getOrderList();
            }
        }, 300);
    });
});
var canAjax = true,
    page = 1;

function getOrderList() {
    if (canAjax) {
        canAjax = false;
        $(".loader-inner").html("<div></div><div></div><div></div><div></div><div></div>");
        $.post(getLists, {
            "vendor_id": vendorId,
            "p": page,
            "time": $("#date_show").text() == "全部日期" ? "" : $("#date_show").text()
        }, function(r) {
            if (r.status == 0) {
                //数据列表为空则直接提示，不进入循环
                if (r.data.length == 0) {
                    if ($("#order_list div").length == 0) {
                        //当天无任何数据时
                        $(".loader-inner").html("查无数据");
                    } else {
                        $(".loader-inner").html("没有更多了");
                    }
                } else {
                    var html = "",
                        list = r.data,
                        //支付方式，补贴手续费状态颜色
                        payType = {
                            0: "",
                            2: "ali",
                            4: "wechat",
                            8: "credit",
                            9: "debit",
                            6: "cash"
                        },
                        subsidy;
                    for (var i = 0; i < list.length; i++) {
                        if (isNaN(parseInt(list[i].poundage_num))) {
                            subsidy = list[i].poundage_num;
                        } else {
                            subsidy = "<i>" + list[i].poundage_num + "</i>";
                        };
                        html += '<div class="panel panel-default"><div class="panel-heading clearfix"><div class="pull-left">订单号：' + list[i].order_id + '</div><div class="pull-right">' + list[i].order_c_time + '</div></div><div class="panel-body"><span><h4>' + list[i].goods_name + '</h4></span><span><h4 class="pay-type ' + payType[parseInt(list[i].bank_card_type)] + '">¥ ' + list[i].order_price_new + '</h4></span><span>' + list[i].user_name + '</span><span class="fee">手续费：<i>' + list[i].order_poundage_w + '</i></span><span>' + list[i].user_cellphone + '</span><span class="subsidy">补贴：' + subsidy + '</span></div></div>';
                    }
                    $("#order_list").append(html);
                    page++;
                }
                canAjax = true;
            } else {
                $(".loader-inner").html("获取数据失败，请重试");
            }
        }, "json");
    }
}


