'use strict';
var infoEdit;
$(function () {
    infoEdit = new WechatWalletInfoEdit();
    infoEdit.goStep(1);
    $('#get_code').on('click', function (e) {
        infoEdit.getConfirmInfo();
        infoEdit.getCheckCode(codeUrl, infoEdit.phoneData.user_phone, $(e.currentTarget));
    });
    //点击验证按钮
    $('#confirm_btn').on('click', function (e) {
        infoEdit.getConfirmInfo();
        infoEdit.postConfirmInfo();
    });
    document.querySelector('#withdrawUserTel').addEventListener('input', function (e) {
        infoEdit.getWithdrawInfo();
        if (infoEdit.bankData.vendor_settle_phone !== infoEdit.oldTel
            && infoEdit.bankData.vendor_settle_phone.length === infoEdit.oldTel.length
            && $('#verify_code').parent().hasClass('hidden')) {
            $('#verify_code').parent().removeClass('hidden');
        } else {
            $('#verify_code').parent().addClass('hidden');
        }
    });
    //用户修改手机号后发送验证码按钮点击事件
    $('#get_new_code').on('click', function (e) {
        infoEdit.getCheckCode(changeCodeUrl, infoEdit.bankData.vendor_settle_phone, $(e.currentTarget));
    });
    //点击提交审核按钮
    $('#withdraw_btn').on('click', function (e) {
        infoEdit.getWithdrawInfo();
        if (infoEdit.checkBankData()) {
            infoEdit.postWithdrawInfo();
        }
    });
});
/**
 *
 * @constructor
 */
function WechatWalletInfoEdit() {
    //更改前的号码
    this.oldTel = '',
    //第一部提交验证的数据
    this.phoneData = {
        vendor_id: '',
        user_phone: '',
        code: ''
    };
    //提款信息
    this.bankData = {
        vendor_id: '',
        code: '',
        vendor_settle_bank_name: '',
        vendor_settle_account_name: '',
        vendor_settle_account_number: '',
        vendor_c_name: '',
        vendor_settle_phone: ''
    };
    this.codeUrl = codeUrl; //发送验证码的地址
    this.changeCodeUrl = changeCodeUrl; //步骤2发送验证码的地址
    this.phoneUrl = phoneUrl;   //验证手机的地址
    this.bankUrl = bankUrl; //提交提款人数据的地址
    this.patrnCheck = new PatternCheck();
    this.timeoutID = null;
}

WechatWalletInfoEdit.prototype = {

    /**
     * 进入第x步
     * @param stepNum: 步骤数字
     */
    goStep: function (stepNum) {
        var stepBlock = $('#step' + stepNum);
        stepBlock.removeClass('hidden');
        $('#progress-tip').find('span:nth-of-type(' + stepNum + ')').addClass('done');
        stepBlock.siblings(':not(#progress-tip)').addClass('hidden');
    },
    //获取电话和验证码
    getConfirmInfo: function () {
        this.phoneData.vendor_id = vendor_id;
        this.phoneData.user_phone = $('#phoneNum').val();
        this.phoneData.code = $('#imageCode').val();
    },
    //获取提款审核数据
    getWithdrawInfo: function () {
        this.bankData.vendor_id = vendor_id;
        this.bankData.code = $('#newImageCode').val();
        this.bankData.vendor_settle_bank_name = $('#bankName').val();
        this.bankData.vendor_settle_account_name = $('#bankUserName').val();
        this.bankData.vendor_settle_account_number = $('#bankAccount').val();
        this.bankData.vendor_c_name = $('#withdrawUserName').val();
        this.bankData.vendor_settle_phone = $('#withdrawUserTel').val();
    },
    /**
     * 设置页面数据
     * @param data
     */
    setWithDrawInfo: function (data) {
        $('#bankName').val(data.vendor_settle_bank_name);
        $('#bankUserName').val(data.vendor_settle_account_name);
        $('#bankAccount').val(data.vendor_settle_account_number);
        $('#withdrawUserName').val(data.vendor_c_name);
        $('#withdrawUserTel').val(data.vendor_settle_phone);
    },
    /**
     * 获取验证码
     * @param url:请求地址
     * @param phoneNum:手机号
     * @param el：被点击的元素
     * @returns {boolean}
     */
    getCheckCode: function (url, phoneNum, el) {
        if (!this.patrnCheck.isPhone(phoneNum)) {
            showDialog(1, '请输入正确的手机号');
            return false;
        }
        if (this.patrnCheck.isPhone(phoneNum, el)) {
            $.ajax({
                url: url,
                type: "POST",
                data: {user_cellphone: phoneNum, vendor_id: vendor_id},
                dataType: "json",
                success: function (data, textStatus, jqXHR) {
                    if (!data.status) {
                        infoEdit.countDown(el);
                        showDialog(0, "发送成功");
                    } else {
                        showDialog(1, data.data.text);
                    }
                },
                error: function (jqXHR, textStatus, errorThrown) {
                    showDialog(1, "请求服务器失败");
                    console.log(jqXHR + "\n" + textStatus + "\n" + errorThrown);
                }
            });
        }
    },
    //提交电话验证码
    postConfirmInfo: function () {
        if (!this.patrnCheck.isPhone(this.phoneData.user_phone)) {
            showDialog(1, '请输入正确的手机号');
            return false;
        }
        if (!this.patrnCheck.isCheckCode(this.phoneData.code)) {
            showDialog(1, '请输入正确的验证码');
            return false;
        }
        if (this.patrnCheck.isPhone(this.phoneData.user_phone) && this.patrnCheck.isCheckCode(this.phoneData.code)) {
            $.ajax({
                url: this.phoneUrl,
                type: "POST",
                data: this.phoneData,
                dataType: "json",
                beforeSend: function () {
                    showDialog(2);
                },
                success: function (data, textStatus, jqXHR) {
                    hideDialog();
                    if (!data.status && data.data) {
                        infoEdit.oldTel = data.data.vendor_settle_phone;
                        infoEdit.bankData.vendor_c_name = data.data.vendor_c_name;
                        infoEdit.bankData.vendor_settle_phone = data.data.vendor_settle_phone;
                        infoEdit.bankData.vendor_settle_account_name = data.data.vendor_settle_account_name;
                        infoEdit.bankData.vendor_settle_account_number = data.data.vendor_settle_account_number;
                        infoEdit.bankData.vendor_settle_bank_name = data.data.vendor_settle_bank_name;
                        infoEdit.setWithDrawInfo(infoEdit.bankData);
                        infoEdit.goStep(2);
                    } else {
                        showDialog(1, data.data);
                    }
                },
                error: function (jqXHR, textStatus, errorThrown) {
                    showDialog(1, "请求服务器失败");
                    console.log(jqXHR + "\n" + textStatus + "\n" + errorThrown);
                }
            });
        }
    },
    //提交提款审核数据
    postWithdrawInfo: function () {
        $.ajax({
            url: this.bankUrl,
            type: "POST",
            data: this.bankData,
            dataType: "json",
            beforeSend: function () {
                showDialog(2);
            },
            success: function (data, textStatus, jqXHR) {
                hideDialog();
                if (!data.status) {
                    infoEdit.goStep(3);
                } else {
                    showDialog(1, data.data);
                }
            },
            error: function (jqXHR, textStatus, errorThrown) {
                showDialog(1, "请求服务器失败");
                console.log(jqXHR + "\n" + textStatus + "\n" + errorThrown);
            }
        });
        return true;
    },
    //用户输入校验
    checkBankData: function () {
        if ('' === this.bankData.vendor_settle_bank_name || '' === this.bankData.vendor_settle_account_number
            || '' === this.bankData.vendor_settle_account_name || '' === this.bankData.vendor_c_name) {
            showDialog(1, '请完善信息');
            return false;
        }
        if (!this.patrnCheck.isBankNum(this.bankData.vendor_settle_account_number)) {
            showDialog(1, '银行卡号只能是16位或19位数字');
            return false;
        }
        if (!this.patrnCheck.isPhone(this.bankData.vendor_settle_phone)) {
            showDialog(1, '请输入正确的手机号');
            return false;
        }
        return true;
    },
    /**
     * 发送验证码倒计时
     * @param targetEl：需要显示文字的元素
     */
    countDown: function (targetEl) {
        var timer = 60;
        if(infoEdit.timeoutID) {
            clearInterval(infoEdit.timeoutID);
            infoEdit.timeoutID = null;
        }
        infoEdit.timeoutID = setInterval(function() {
            if (timer > 0) {
                targetEl.text("重新发送（" + timer + "）").attr("disabled", true);
                timer--;
            } else {
                clearInterval(infoEdit.timeoutID);
                infoEdit.timeoutID = null;
                targetEl.text("获取验证码").attr("disabled", false);
            }
        }, 1000);
    }
};


