var makeup_id="",sign_type="";
$(".makeup_btn").click(function () {
    makeup_id=$(this).parent().attr("data");
    sign_type=$(this).attr("data");
    console.log(makeup_id,sign_type);
    $("#makeup-dialog .modal-body").text("您确定已补课了吗？");
    $("#makeup-dialog").modal({});
    // makeup_view(makeup_id,sign_type);
})
$(".not_btn").click(function () {
    makeup_id=$(this).parent().attr("data");
    sign_type=$(this).attr("data");
    console.log(makeup_id,sign_type);
    $("#makeup-dialog .modal-body").text("您确定不补课了吗？");
    $("#makeup-dialog").modal({});
    // makeup_view(makeup_id,sign_type);
})
