var canScroll;
$("#classlist_btn").click(function() {
    canScroll = false;
});
$("#classcomment_btn").click(function() {
    canScroll = true;
    if ($("#courses-list ul li").length == 2) {
        view();
    }
})
var noMore = false;
var page = 1;

function view() {
    $.post(url, {
        p: page
    }, function(obj) {
        var html = "";
        console.log(obj.error_info.length);
        if (obj.error_info.length > 0) {
            $.each(obj.error_info, function(i, p) {
                html += '<li class="list-group-item"><div class="container-fluid"><div class="row comments-head"><div class="col-xs-6">' + p.geval_deta_time + '</div><div class="col-xs-6 text-right">';
                if (p.flag == 0) {
                    html += '<span>已回评</span>';
                } else {
                    html += '<a href="' + btnUrl + '&class_id=' + p.class_id + '&student_id=' + p.student_id + '&geval_deta_id=' + p.geval_deta_id + '" class="btn btn-primary btn-sm">我要回评</a>';
                }
                html += '</div></div><div class="row comments-body"><div class="col-xs-12">' + p.geval_deta_text + '</div><div class="col-xs-12">';
                if (p.pic !== "") {
                    html += '<img src="' + p.pic + '" alt="">';
                }
                html += '</div><div class="col-xs-12"><span>来自</span><span>' + p.vendor_name + '-' + p.user_username + '</span></div> <div class="col-xs-12"><span>' + p.goods_name + '</span></div></div> </div></li>';
            })
            $("#comment ul").append(html);
        } else {
            if (noMore == false) {
                html = '<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价';
                $(".loader").html(html);
                noMore = true;
            }
        }
        if (obj.error_info.length < 10) {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        page++;
        canScroll = true;
    }, 'json')
}
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h && canScroll) {
        canScroll = false;
        view();
    }
});
$(document).on("click", "img", function(e) {
    e.preventDefault();
    var imgs = $("#comment ul img");
    for (var i = 0, urls = []; i < imgs.length; i++) {
        urls.push(imgs[i].src);
    }
    wx.previewImage({
        current: e.target.src,
        urls: urls
    });
});


