$(".my_show_icon").addClass("active");
var img_h=$("#show_index .show_list .col-xs-4 img").width();
$("#show_index .show_list .col-xs-4 img").height(img_h);
var delete_id;
$(".show_delete_icon").click(function () {
    showDialog(2);
    delete_id=$(this).attr("data-id");
    $(".modal-content").html('<div class="modal-body">你确定要删除吗？</div><div class="modal-footer"><button type="button" class="btn btn-default" data-dismiss="modal">取消</button><button type="button" class="btn btn-primary" id="delete-confirm">确定</button></div>');
});
$(document).ready(function (e) {
    $(document).on("click","#delete-confirm",function () {
        $(".modal-content").html('<div class="modal-body"></div>');
        showDialog(2);
        $.post(deleteurl,{article_id:delete_id},function (result) {
            if(result.status==0){
                showDialog(0);
                window.location.reload();
            }else {
                showDialog(1);
            }
        })
    })
});


