'use strict';
var newPwd = null;
$(function () {
    newPwd = new NewPwd();
    //获取验证码
    $('#get_new_code').on('click', function (e) {
        newPwd.getData();
        newPwd.getCheckCode($(this));
    });
    //提交新密码
    $('#submit_btn').on('click', function (e) {
        newPwd.getData();
        if (!newPwd.checkPattern()) {
            return false;
        }
        newPwd.postData();
    });
    document.querySelector('#newPwd2').addEventListener('input', function (e) {
        newPwd.getData();
        if (newPwd.data.pwd !== newPwd.data.repwd && newPwd.data.pwd.length === newPwd.data.repwd.length) {
            $('#newPwd2').parent().find('span.text-danger').text('两次密码不一致').removeClass('hidden');
        } else {
            $('#newPwd2').parent().find('span.text-danger').text('').addClass('hidden');
        }
    });
});
function NewPwd() {
    this.codeUrl = code_url;
    this.postUrl = post_url;
    this.data = {
        vendor_id: '',
        pwd: '',
        repwd: '',
        user_cellphone: '',
        code: ''
    };
    this.checkPatn = new PatternCheck();
    this.timeoutID = null;
}
NewPwd.prototype = {
    /**
     * 获取页面表单数据
     */
    getData: function () {
        this.data.vendor_id = vendor_id;
        this.data.pwd = $('#newPwd').val();
        this.data.repwd = $('#newPwd2').val();
        this.data.user_cellphone = $('#withdrawUserTel').val();
        this.data.code = $('#newImageCode').val();
    },
    /**
     * 校验输入数据
     * @returns {boolean}
     */
    checkPattern: function () {
        if (!this.checkPatn.isSimplePwd(this.data.pwd)) {
            showDialog(1, '密码只能为6位数字');
            return false;
        }
        if (this.data.pwd !== this.data.repwd) {
            showDialog(1, '两次密码不一致');
            return false;
        }
        if (!this.checkPatn.isPhone(this.data.user_cellphone)) {
            showDialog(1, '请输入正确的手机号');
            return false;
        }
        if (!this.checkPatn.isCheckCode(this.data.code)) {
            showDialog(1, '请输入正确的验证码');
            return false;
        }
        return true;
    },
    /**
     * 获取验证码
     * @returns {boolean}
     */
    getCheckCode: function (self) {
        if (!this.checkPatn.isPhone(this.data.user_cellphone)) {
            showDialog(1, '请输入正确的手机号');
            return false;
        }
        $.ajax({
            url: this.codeUrl,
            type: 'POST',
            dataType: 'json',
            data: {
                vendor_id: this.data.vendor_id,
                user_cellphone: this.data.user_cellphone
            }
        })
            .done(function (data, textStatus, jqXHR) {
                if (!data.status) {
                    showDialog(0, '发送成功');
                    newPwd.countDown(self);
                } else {
                    showDialog(1, data.data);
                }
                console.log(jqXHR + "\n" + textStatus + "\n" + data);
            })
            .fail(function (jqXHR, textStatus, errorThrown) {
                showDialog(1, '发送失败');
                console.log(jqXHR + "\n" + textStatus + "\n" + errorThrown);
            });
    },
    /**
     * 提交新密码
     */
    postData: function () {
        $.ajax({
            url: this.postUrl,
            type: 'POST',
            dataType: 'json',
            data: this.data
        })
            .done(function (data, textStatus, jqXHR) {
                if (!data.status) {
                    showDialog(0, '修改成功');
                    setTimeout(function() {
                        location.replace(document.referrer)
                    }, 2000);
                } else {
                    showDialog(1, data.data);
                }
                console.log(jqXHR + "\n" + textStatus + "\n" + data);
            })
            .fail(function (jqXHR, textStatus, errorThrown) {
                showDialog(1, '修改失败');
                console.log(jqXHR + "\n" + textStatus + "\n" + errorThrown);
            });
    },
    /**
     * 发送验证码倒计时
     * @param targetEl：需要显示文字的元素
     */
    countDown: function (targetEl) {
        var timer = 60;
        if(newPwd.timeoutID) {
            clearInterval(newPwd.timeoutID);
            newPwd.timeoutID = null;
        }
        newPwd.timeoutID = setInterval(function() {
            if (timer > 0) {
                targetEl.text("重新发送（" + timer + "）").attr("disabled", true);
                timer--;
            } else {
                clearInterval(newPwd.timeoutID);
                newPwd.timeoutID = null;
                targetEl.text("获取验证码").attr("disabled", false);
            }
        }, 1000);
    }
};


