/**
 * 显示提示框
 * @param  {int} status 0成功1失败2加载中
 * @param  {string} txt 提示内容
 * @param  {int} delay 时间(s)
 */
function showDialog(status, txt, delay) {
    var content, ico, $modal = $("#loading-dialog");
    switch (status) {
        case 0:
            ico = "<span class='glyphicon glyphicon-ok'></span>";
            content = "操作成功";
            break;
        case 1:
            ico = "<span class='glyphicon glyphicon-remove'></span>";
            content = "操作失败";
            break;
        case 2:
            ico = "<span class='glyphicon glyphicon-refresh'></span>";
            content = "加载中";
            break;
        default:
            alert(status, txt);
    };
    if (txt !== undefined) {
        $modal.find(".modal-body").html(ico + txt);
    } else {
        $modal.find(".modal-body").html(ico + content);
    };
    $modal.modal({
        backdrop: 'static',
        keyboard: false
    });
    if (status != 2) {
        if (delay == undefined) {
            delay = 2;
        };
        var t = setTimeout(function() {
            $modal.modal('hide');
        }, delay * 1000);
    }
};
/**
 * 隐藏提示框
 */
function hideDialog() {
    $("#loading-dialog").modal('hide');
}
// todo sign不再显示
$(".sign_tip button span").click(function() {
    window.localStorage.setItem("close", true);
});
window.onload = function() {
    var close = window.localStorage.getItem("close");
    if (close == "true") {
        $(".sign_tip").hide();
    }
}



