var phone=$("#phone").text();
$(document).on("click","#remove_bind",function () {
    showDialog(2);
    $.post(removebind,{phone:phone},function (result) {
        if(result.status==0){
            showDialog(0);
            window.location.href=myWeChat;
        }else {
            showDialog(1,result.data);
        }
    })
})
$(".modify_btn").click(function () {
    var name_old=$("#name_old").html();
    $("#name_new").val(name_old);
});
$("#modify_btn").click(function () {
    // console.log("click",$(this));
    var phone=$("#phone").text();
    var name_new=$("#name_new").val();
    if(name_new!=""){
    $.post(editUserInfo,{user_phone:phone,user_name:name_new},function (result) {
        if(result.status==0){
            $("#name_old").html(name_new);
        }
    })
    }
    $("#myModal").modal('hide');
});


