var user_cellphone=$("#phone").text();
$(document).on("click","#remove_bind",function () {
    showDialog(2);
    $.post(removebind,{user_cellphone:user_cellphone},function (result) {
        if(result.error_code==0){
            showDialog(0);
            window.location.href=myWeChat;
        }else {
            showDialog(1,"解除失败，请重试");
        }
    })
});


