var date = new Date();
var mon = date.getMonth() + 1;
var day = date.getDate();
var nowDay = date.getFullYear() + "-" + (mon<10?"0"+mon:mon) + "-" +(day<10?"0"+day:day);
$(function () {
    var now = new Date(),
        minDate = new Date(now.getFullYear() - 1, 0, 1);

    $('#date_chk').mobiscroll().date({
        theme: 'mobiscroll',
        lang: 'zh',
        display: 'modal',
        dateFormat: 'yyyy-mm-dd',
        minDate: minDate,
        multiline: 2,
        maxDate: new Date(now.getFullYear(), now.getMonth(), now.getDate())
    });
});
$("#date_chk").change(function () {
    var date_chk=$("#date_chk").val();
    var school_chk=$("#school_list").val();
    console.log(date_chk);
    console.log(school_chk);
    window.location.href=infoUrl+"&vendor_id="+school_chk+"&get_time="+date_chk;
});
$('#school_list').mobiscroll().select({
    theme: 'mobiscroll',
    lang: 'zh',
    display: 'modal',
    multiline: 3,
    height: 50
});
$("#school_list_dummy").addClass("over-text");
$("#school_list").change(function () {
    var date_chk=$("#date_chk").val();
    var school_chk=$("#school_list").val();
    if(date_chk==""){
        date_chk=nowDay;
    }
    console.log(school_chk);
    console.log(date_chk);
    window.location.href=infoUrl+"&vendor_id="+school_chk+"&get_time="+date_chk;
});


