$("#get_code").click(function () {
    var phone=$("#phone_num").val();
    if(phone!="") {
        showDialog(2);
        $.post(checkmobileurl, {phone: phone}, function (result) {
            if (parseInt(result.status) == 0) {
                $.post(sendmsgurl, {phone: phone}, function (result) {
                    if (parseInt(result.status) == 1) {
                        var codeError = result.data;
                        showDialog(1, codeError, 2);
                    } else {
                        showDialog(0, "发送成功");
                        settime($("#get_code"));
                    }
                })
            }
            else {
                var phoneError = result.data;
                showDialog(1, phoneError, 2);
            }
        }, "json")
    }
    else{
        $(".code_error").html("请输入手机号");
    }
})
