var countdown=60;
function settime(obj) {
    // console.log(obj);
    if (countdown == 0) {
        console.log(obj);
         obj[0].removeAttribute("disabled");
         obj.html("获取验证码");
        countdown = 60;
        return;
    } else {
         obj[0].setAttribute("disabled", true);
         obj.html("重新发送(" + countdown + "s)");
        countdown--;
    }
    setTimeout(function() {
            settime(obj) }
        ,1000)
}


