$(function () {
    var now = new Date(),
        minDate = new Date(now.getFullYear(), 0, 1);

    $('#date_chk').mobiscroll().date({
        theme: 'mobiscroll',
        lang: 'zh',
        display: 'modal',
        minDate: minDate,
        multiline: 2,
        dateFormat: 'yyyy-mm',
        dateOrder :'yymm',
        maxDate: new Date(now.getFullYear(), now.getMonth(), now.getDate())
    });
});
$('#school_list').mobiscroll().select({
    theme: 'mobiscroll',
    lang: 'zh',
    display: 'modal',
    multiline: 3,
    height: 50
});
var count_page=1;
var vendor_id=$("#vendor_id").attr("data-id");
var date = new Date();
var mon = date.getMonth() + 1;
var get_time = date.getFullYear() + "-" + (mon<10?"0"+mon:mon);
var list_canscroll=false;
function show_list() {
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(getMasterCount,{p:count_page,get_time:get_time,vendor_id:vendor_id},function (result) {
        if(result.status==0){
            result=result.data;
            var html="";
            $.each(result, function(i, data) {
                // console.log(data);
                html+='<div class="panel panel-default"><div class="panel-heading"><h4>';
                html+=data.teacher_name;
                html+='</h4></div><ul class="panel-body list-group"><li class="list-group-item"><p>班课</p><div class="row"><div class="col-xs-7">授课次数（节）：</div><div class="col-xs-5">';
                html+=data.class_sign_num;
                html+='</div></div><div class="row"><div class="col-xs-7">消耗课时（人/节）：</div><div class="col-xs-5">';
                html+=data.class_sign_hour;
                html+='</div></div></li><li class="list-group-item"><p>1对1</p><div class="row"> <div class="col-xs-7">授课次数（节）：</div><div class="col-xs-5">';
                html+=data.single_sign_num;
                html+='</div></div><div class="row"><div class="col-xs-7">消耗课时（人/节）：</div><div class="col-xs-5">';
                html+=data.single_sign_hour;
                html+='</div> </div> </li> </ul> </div>';
            });
            $(".teacher_list").append(html);
            count_page++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多数据了');
            }
        }else{
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多数据了');
        }
        list_canscroll=true;
    },'json');
}
show_list();
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h &&($("body").height() >= h)&&list_canscroll) {
        list_canscroll=false;
        show_list();
    }
});
