$('#myTabs a').click(function(e) {
    e.preventDefault();
    $(this).tab('show');
})

// todo 校长端家长评价
$("#assessment_tab a").on("click",function (e) {
    e.preventDefault();
    $($(this).attr("href")).show().siblings().hide();
})

var canScroll = true;
var page = 1;
function view() {
    $.post(classurl, {
        p: page
    }, function(result) {
        result = result.error_info;
        var html = "";
        $.each(result, function(i, class_comment) {
            html += '<div class="list-group-item"><div class="row"><div class="col-xs-7"><p class="course_name">'+class_comment.goods_name+'</p>';
            html += '<p class="teacher_name">上课老师：<span>'+class_comment.teacher_name+'</span></p>';
            var time = class_comment.class_time;
                html += '<p class="course_time">上课时间：';
            for (var i = 0; i < time.length; i++) {
                    html += '<span>'+time[i]+'</span>'
            }
            html += '</p></div><div class="col-xs-5"><ul class="grade">';
            var star = Math.floor(class_comment.geval_t_point);
            for (var i = 0; i < star; i++) {
                html += '<li class="active"></li>';
            }
            for (var i = 0; i < 5 - star; i++) {
                html += '<li></li>';
            }
            html += '</ul><p>综合得分：<span>'+(class_comment.geval_t_point==null?0:class_comment.geval_t_point)+'</span>分</p></div></div></div>';
        })
        $("#class .loader").before(html);
        if (result.length < 10) {
            $(".loader").html('<img src="'+imgurl+'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        page++;
        canScroll = true;
    }, "json")
}
view();
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h && canScroll) {
        canScroll = false;
        view();
    }
})


