$(".advisory_icon").addClass("active");
var pagenum = 1,
    h,
    timer,
    newH,
    data = {
        pagenum: pagenum
    };
var canScroll = true;
// todo 日期转换
function dateFomat(date) {
    date = new Date(date);
    date = date.getFullYear() + '-' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
    return date;
}
// todo 页面列表请求数据
function show_list() {
    if (!canScroll) {
        return false;
    };
    canScroll = false;
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(advisoryurl, data, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.list;
            var html = "";
            $.each(result, function(i, list) {
                html+='<div class="list-group-item"><a href="javascript:" data-phone="';
                html+=list.enrol_cellphone;
                html += '" data-id="';
                html += list.id;
                html+='" class="row';
                if (parseInt(list.type_id) == 0) {
                    html+=' pending user_msg"><div class="col-xs-6"><h4 class="user_name">';
                    html+=list.enrol_name;
                    html+='</h4><span>来源：</span><span class="activity_name">';
                    html+=list.article_title;
                    html+='</span></div><div class="col-xs-6"><h4 class="text-right">待处理';
                }else {
                    html+=' dealing user_msg"><div class="col-xs-6"><h4 class="user_name">';
                    html+=list.enrol_name;
                    html+='</h4><span>来源：</span><span class="activity_name">';
                    html+=list.article_title;
                    html+='</span></div><div class="col-xs-6"><h4 class="text-right">已处理';
                }
                html+='</h4><span class="pull-right user_date">';
                var date = dateFomat(list.create_time * 1000);
                html+=date;
                html+='</span></div></a><div class="row"><a class="phone_to col-xs-6" href="tel:' ;
                html+=list.enrol_cellphone;
                html+='"></a><a href="javascript:" class="col-xs-6 delete_btn"></a></div></div>';
            });
            $("#show_advisory .list-group").append(html);
            data.pagenum++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多咨询记录了');
            }
        } else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多咨询记录了');
        }
        canScroll = true;
    }, 'json');
}

$(document).ready(function(e) {
    show_list();

    // todo 详情

    $(document).on("click", "#show_advisory .user_msg", function() {
        var user_name = $(this).find(".user_name").text(),
            activity_name = $(this).find(".activity_name").text(),
            user_date = $(this).find(".user_date").text(),
            user_phone = $(this).attr("data-phone"),
            user_id = $(this).attr("data-id");
        if ($(this).hasClass("pending")) {
            showDialog(2);
            $(".modal-content").html('<div class="modal-body container"><div class="row"><div class="col-xs-4">姓名:</div><div class="col-xs-8" >' + user_name + '</div></div><div class="row"><div class="col-xs-4">电话:</div><div class="col-xs-8">' + user_phone + '</div></div><div class="row"><div class="col-xs-4">提交时间:</div><div class="col-xs-8">' + user_date + '</div></div><div class="row"><div class="col-xs-4">来源:</div><div class="col-xs-8">' + activity_name + '</div></div><div class="row"><div class="col-xs-4">状态:</div><div class="col-xs-8 pending_color">未处理</div></div></div><div class="btn-group"><button type="button" class="btn btn-primary deal_btn" data-id="' + user_id + '">已处理</button><button type="button" class="btn btn-default" data-dismiss="modal">关闭</button></div>');
        } else {
            showDialog(2);
            $(".modal-content").html('<div class="modal-body container"><div class="row"><div class="col-xs-4">姓名:</div><div class="col-xs-8" >' + user_name + '</div></div><div class="row"><div class="col-xs-4">电话:</div><div class="col-xs-8">' + user_phone + '</div></div><div class="row"><div class="col-xs-4">提交时间:</div><div class="col-xs-8">' + user_date + '</div></div><div class="row"><div class="col-xs-4">来源:</div><div class="col-xs-8">' + activity_name + '</div></div><div class="row"><div class="col-xs-4">状态:</div><div class="col-xs-8">已处理</div></div></div><button type="button" class="btn btn-default btn-block" data-dismiss="modal">关闭</button>');
        }
    });

    // todo 删除
    var delete_id;
    $(document).on("click",".delete_btn",function () {
        showDialog(2);
        delete_id=$(this).parent().prev("a").attr("data-id");
        $(".modal-content").html('<div class="modal-body">你确定要删除吗？</div><div class="btn-group"><button type="button" class="btn btn-primary " id="delete-confirm">确定</button><button type="button" class="btn btn-default" data-dismiss="modal">取消</button></div>');
    });
    $(document).on("click","#delete-confirm",function () {
        $(".modal-content").html('<div class="modal-body"></div>');
        showDialog(2);
        $.post(deleteurl,{id:delete_id},function (result) {
            if(result.status==0){
                showDialog(0);
                window.location.reload();
            }else {
                showDialog(1);
            }
        })
    })
});

// todo 滚动事件
$(window).scroll(function() {
    clearTimeout(timer); //如果操作时已经有了延迟执行, 则取消该延迟执行
    timer = setTimeout(function() { //设定新的延迟执行
        h = $(document).height();
        newH = $(window).scrollTop() + window.screen.availHeight;
        if (newH >= h && $("body").height() == h) {
            show_list();
        }
    }, 500)
});
// todo 筛选
// todo 状态筛选
$(".show_status li a").click(function(e) {
    e.preventDefault();
    var way_id = $(this).attr("data-id");
    $(".status_btn").attr("data-id",way_id);
    var act_name=$(".activity_btn").find("span.over-text").text();
    if(act_name=="全部活动"){
        act_name="";
    }
    data.pagenum = 1;
    if (way_id == 1) {
        data = {
            way_id: 1,
            pagenum: pagenum,
            article_title:act_name
        }
        $("#dLabel").html('<span class="over-text">已处理</span><span class="glyphicon glyphicon-menu-down"></span>')
    } else if (way_id == 2) {
        data = {
            way_id: 2,
            pagenum: pagenum,
            article_title:act_name
        }
        $("#dLabel").html('<span class="over-text">待处理</span><span class="glyphicon glyphicon-menu-down"></span>')
    }
    if (way_id == '') {
        data = {
            pagenum: pagenum,
            article_title:act_name
        }
        $("#dLabel").html('<span class="over-text">全部客户</span><span class="glyphicon glyphicon-menu-down"></span>')
    }
    $("#show_advisory .list-group").html("");
    show_list();
});
$(document).on("click", ".dropdown-toggle", function() {
    if ($(this).attr("aria-expanded") == "true") {
        $("body").css("height", window.screen.availHeight + "px");
    } else {
        $("body").attr("style", "");
    }
});
// todo 活动筛选
$(document).on("touchend", ".show_activity li", function(e) {
    e.preventDefault();
    var activityName = $(this).find(".col-xs-8").text();
    $("#dLabel_r").html('<span class="over-text">' + activityName + '</span><span class="glyphicon glyphicon-menu-down"></span>');
    if(activityName=="全部活动"){
        activityName="";
    }
    var sta_id=$(".status_btn").attr("data-id");
    data.pagenum = 1;
        data = {
            article_title: activityName,
            pagenum: pagenum,
            way_id:sta_id
        };
        $("#show_advisory .list-group").html("");
        show_list();
    $(this).parents(".dropdown").click();
});
// todo 筛选活动获取数据
$.post(acticalurl, function(result) {
    if (parseInt(result.status) == 0) {
        result = result.title_list;
        var list_html = "";
        $.each(result, function(i, list) {
            list_html += '<li class="row"><a href="javascript:"><div class="col-xs-8 over-text">';
            list_html += list.article_title;
            list_html += '</div><div class="col-xs-4 text-right">';
            var date = dateFomat(list.create_time * 1000);
            list_html += date;
            list_html += '</div></a></li>';
        });
        $("#dLabel_r+.dropdown-menu").html(list_html);
        $("#dLabel_r+.dropdown-menu").append('<li class="row"><a href="javascript:"><div class="col-xs-8 over-text">全部活动</div></a></li>')
    } else {
        $("#dLabel_r+.dropdown-menu").append('<li class="row"><a href="javascript:"><div class="col-xs-8 over-text">全部活动</div></a></li>');
    }
}, 'json');
// todo 状态处理
$(document).on("click", ".deal_btn", function() {
    var id = $(this).attr("data-id");
    $(".modal-content").html('<div class="modal-body"></div>');
    showDialog(2);
    $.post(handleurl, {
        id: id
    }, function(result) {
        if (parseInt(result.status) == 0) {
            showDialog(0, "处理成功");
            window.location.reload();
        } else {
            showDialog(1, result.data);
        }
    })
});

// todo 遮罩层
$("#show_advisory .menu_head .dropdown").on("touchend",function () {
    if($(this).hasClass("open")){
        $("body").removeClass("black");
    }else {
        $("body").addClass("black");
    }
});


