$(document).on("touchend click", "#rating span", function() {
    $(this).nextAll().removeClass("hover").end().addClass("hover").prevAll().addClass("hover");
    $("#fraction i").text($(this).index() + 1)
});
$("#geval_deta_text").keyup(function(){
    var txt=$.trim($(this).text());
    if(txt.length>50){
        txt=txt.substring(0,txt.length-1);
    }
});
$("#review-btn").click(function() {
    var point=$("#fraction i").text();
    if (point<1) {
        showDialog(1, "请给老师打分", 2);
        return false;
    };
    var cont = $.trim($("#geval_deta_text").val());
    if (cont.length < 2) {
        showDialog(1, "回复内容至少填写2个字", 2);
        return false;
    };
    var send={};
    send.student_id=$("#student_id").val();
    send.class_id=$("#class_id").val();
    send.geval_deta_id=$("#geval_deta_id").val();
    send.geval_deta_text=cont
    send.geval_t_point=point;
    showDialog(2);
    $.post(url, send, function(data){
        if (parseInt(data.error_code)===0) {
            showDialog(0);
            window.location.href=commenturl;
        }else{
            showDialog(1);
        }
    },"json");
});



