var stu_name,stu_phone,stu_id,start_time,end_time;
$(".save_btn").click(function () {
    if($("input[type=checkbox]:checked").length==0){
        showDialog(1,"请阅读《参保条款详情》并同意该协议");
    }else {
    stu_name=$(".stu_name").val();
    stu_phone=$(".stu_phone").val();
    stu_id=$(".stu_id").val();
    start_time=$(".date_chk label").text();
    end_time=$(".end_time").text();
    if(stu_name!=""&&stu_phone!==""&&stu_id!=""&&end_time!=""){
        // todo 手机号验证
        if((/^1[34578]\d{9}$/.test(stu_phone))){
            // todo 身份证验证
            if(checkId(stu_id)) {
                showDialog(2);
                $(".modal-content").html('<div class="modal-body container"><div class="row"><h5 class="col-xs-12 text-center">保险信息</h5></div><div class="row"><div class="col-xs-4">保单生效时间:</div><div class="col-xs-8">'
                    + start_time
                    + '</div></div><div class="row"><div class="col-xs-4">保单终止时间:</div><div class="col-xs-8">'
                    + end_time
                    + '</div></div><div class="row"><h5 class="col-xs-12 text-center">被保人信息</h5></div><div class="row"><div class="col-xs-4">学生姓名:</div><div class="col-xs-8">'
                    + stu_name
                    + '</div></div><div class="row"><div class="col-xs-4">电话号码:</div><div class="col-xs-8">'
                    + stu_phone
                    + '</div></div><div class="row"><div class="col-xs-4">身份证号:</div><div class="col-xs-8">'
                    + stu_id
                    + '</div></div><div class="row"><div class="col-xs-12 note_span">请确认您的信息，一旦提交无法更改！</div></div></div><div class="modal-footer"><button type="button" class="btn btn-default dismiss" data-dismiss="modal">修改</button><button type="button" class="btn btn-primary" id="modify_btn" > 提交</button></div>')
            }else {
                showDialog(1,"请填写正确的身份证号");
            }
        }else {
            showDialog(1,"请填写正确的手机号");
        }

    }else {
        showDialog(1,"请完善您的信息");
    }
    }
});
