$(document).ready(function (e) {
    $(document).on("click",".dismiss",function () {
        $(".modal-content").html('<div class="modal-body"></div>');
    })
});
$(document).ready(function (e) {
    $(document).on("click", "#modify_btn", function(){
        $.post(insuranceaddurl,{
            order_id:$("#my_insurance").attr("data-id"),
            insurance_start_date:start_time,
            insurance_end_date:end_time,
            insurance_name:stu_name,
            insurance_phone:stu_phone,
            insurance_card_id:stu_id
        },function (result) {
            $(".modal-content").html('<div class="modal-body"></div>');
            if(result.error_code==0){
                showDialog(0);
                window.location.href=insuranceindexurl;
            }else {
                // showDialog(1,"提交失败，请重新提交");
                showDialog(1,result.error_info);
            }
        })
    });
})
// todo 身份证验证方法
var vcity={ 11:"北京",12:"天津",13:"河北",14:"山西",15:"内蒙古",
    21:"辽宁",22:"吉林",23:"黑龙江",31:"上海",32:"江苏",
    33:"浙江",34:"安徽",35:"福建",36:"江西",37:"山东",41:"河南",
    42:"湖北",43:"湖南",44:"广东",45:"广西",46:"海南",50:"重庆",
    51:"四川",52:"贵州",53:"云南",54:"西藏",61:"陕西",62:"甘肃",
    63:"青海",64:"宁夏",65:"新疆",71:"台湾",81:"香港",82:"澳门",91:"国外"};
var Month={ 1:31,2:28,3:31,4:30,5:31,6:30,7:31,8:31,9:30,10:31,11:30,12:31};
var IDint=[7,9,10,5,8,4,2,1,6,3,7,9,10,5,8,4,2];
var IDch=['1','0','X','9','8','7','6','5','4','3','2'];
function checkId(str) {
    str=str.replace(/\s/g, "");
    if(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|[xX])$/.test(str)){
        var pro=parseInt(str.substring(0,2));
        var year=parseInt(str.substring(6,10));
        var month=parseInt(str.substring(10,12));
        var day=parseInt(str.substring(12,14));
        if (((year % 4) == 0)
            && ((year % 100) != 0
            || ((year % 400) == 0))){
            month[2]=29;
        }else {
            month[2]=28;
        }
        var num=0;
        for(var i=0;i<17;i++){
            num+=parseInt(str.substring(i,i+1))*IDint[i];
        }

        var code18=str.substring(17,18);
        if(code18=="x"){
            IDch[2]="x";
        }else if(code18=="X"){
            IDch[2]="X";
        }
        var age=new Date().getFullYear();
        age=age-year;
        console.log(age<0||age>110,day>Month[month],day>month[month],vcity[pro]==undefined,code18!=IDch[num%11]);
        if(age<0||age>110//年龄
            ||Month[month]==undefined//月
            ||day>Month[month]//日
            ||vcity[pro]==undefined//省
            ||code18!=IDch[num%11]//校验位
        ){
            return false;
        }
        return true;
    }
    return false;
}


