var effective=parseFloat($(".effective b").html());
$("#amount").attr("placeholder","最大可提现 "+effective+"元");
$("#pwd").click(function () {
    if(parseFloat($("#amount").val())>effective){
        showDialog(1, "可提现金额不足");
        $("#amount").val("");
    }
});
$("#withdraw_btn").click(function () {
    var vendor_id=$("#vendor_id").val();
    var passwd=$("#pwd").val();
    var fina_cash=$("#amount").val();
    if(passwd!=""&&fina_cash!=""){
        $(this).attr("disabled",true);
        $.post(posturl, {vendor_id: vendor_id,fina_cash:fina_cash,passwd:passwd}, function (result) {
        if(parseInt(result.error_code) == 0){
            showDialog(0,"提现成功，款项将在1-2个工作日提现至对应账户");
            $(".effective b").html(parseFloat(effective-fina_cash)+"￥");
        }else if (parseInt(result.error_code) === 1010){
            $(this).attr("disabled");
            showDialog(1,result.error_info);
        } else {
            showDialog(1,result.error_info);
        }
    },"json")}else {
        showDialog(1,"提现金额或密码不能为空");
    }
});


