$(".choose_btn").click(function (e) {
    e.preventDefault();
    $("#note_detail").hide();
    $("#note_receive").show();
});
$(".choose_save").click(function () {
    var input_list=$("input[type=checkbox]:checked");
    if(input_list.length==0){
        showDialog(1,"请至少选择一个发送对象");
    }else {
    for(var i=0,span_list="";i<input_list.length;i++){
        var data_id=($(input_list[i]).attr("data-id"));
        span_list+='<span data-id="'+data_id+'">'+$(input_list[i]).attr("data-name")+'</span>';
    }
    $(".choose_btn").html(span_list);
    $("#note_detail").show();
    $("#note_receive").hide();
    }
});
$(function () {
    var now = new Date(),
        minDate = new Date(now.getFullYear() , 0, 1);

    $('#date_chk').mobiscroll().datetime({
        theme: 'mobiscroll',
        lang: 'zh',
        display: 'modal',
        dateFormat: 'yyyy-mm-dd',
        minDate:new Date(now.getFullYear(), now.getMonth(), now.getDate(),now.getHours(),now.getMinutes()),
        multiline: 2,
        timeWheels: 'HHii',
    });
});
$("#timing").click(function () {
    $(".timing").show();
})
$("#at_once").click(function () {
    $(".timing").hide();
    $(".btn-primary").val("发送");
})
$("#date_chk").change(function () {
    $(".date_chk label").text($(this).val());
    $("#timing").click();
});
$("#note_detail .btn-primary").click(function () {
    var mid=$("#note_detail form").attr("data-id"),
        class_id=[],
        send_type=1,
        msg_title=$(".msg_title").val(),
        msg_content=$(".msg_content textarea").val(),
        send_time="";
    if(send_type==0){
        send_time=$(".date_label").text();
    }
    for(var i=0;i<$(".choose_btn span").length;i++){
        class_id.push($($(".choose_btn span")[i]).attr("data-id"));
    }
    console.log(class_id,msg_content,msg_title);
    if(class_id!=[]&&msg_title!=""&&msg_content!=""){
        showDialog(2);
        $.post(sendmsgurl,{mid:mid,class_id:class_id,send_type:send_type,title:msg_title,content:msg_content,send_time:send_time},function (result) {
            if(result.status==0){
                showDialog(0);
                window.location.href=getmsgurl;
            }else {
                showDialog(1);
            }
        })
    }else {
        showDialog(1,"请完善您的信息");
    }
})
$("#search_btn").click(function () {
    var class_name=$(".search_input").val();
    if(class_name!=""){
        showDialog(2);
        $.post(getclassurl,{class_name:class_name},function (result) {
            if(result.status==0){
                showDialog(0);
                result=result.data;
                var html="";
                $.each(result,function (i,data) {
                    html+='<label><div class="row"><div class="col-xs-2"><input type="checkbox" data-id="';
                    html+=data.id;
                    html+='" data-name="';
                    html+=data.name;
                    html+='"></div><div class="col-xs-4"><h4>';
                    html+=data.name;
                    html+='</h4>';
                    for(var j=0;j< data.class_time.length;j++){
                        html+='<p>';
                        html+=data.class_time[j].weeks;
                        html+=':';
                        html+=data.class_time[j].start_time;
                        html+='</p>';
                    }
                    html+='</div><div class="col-xs-6 over-text">';
                    html+=data.vendor_name;
                    html+='</div></div></label>';
                })
                $(".class_list").html(html);
                if($(".class_list label").length==0){
                    $(".class_list").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有匹配的查询结果');
                }
            }else {
                showDialog(1,"系统繁忙，请稍后重试");
            }
        })
    }else {
        showDialog(1,"班级名称不能为空");
    }
})




