$(document).on("touchend", "#student-list div", function(event) {
    event.preventDefault();
    // var $this = $(this),
    //     text = "",
    //     i = $this.index();
    // if ($this.find("h6").text() !== "") {
    //     if ($this.hasClass("sign-choose")) {
    //         text = "到课"
    //     } else {
    //         text = "未到"
    //     }
    //     $this.toggleClass("sign-choose").find("h6").text(text);
    // } else {
    //     return false;
    // }
    var $this=$(this),sign_status=$this.attr("data");
    switch (sign_status){
        case "0":
            $this.attr("data",1).toggleClass("sign-choose").find("h6").text("到课");
            break;
        case "1":
            $this.attr("data",0).toggleClass("sign-choose").find("h6").text("未到");
            break;
        case "2":
            showDialog(1,"学员已经补课，不可修改");
            break;
        case "3":
            showDialog(1,"学员已经操作不补课，不可修改");
            break;
    };

});
$("#sign-btn").click(function() {
    var $btn = $(this).button('loading');
    showDialog(2);
    var send = {},
        studentArray = new Array();
    send.cid = cid;
    $("#student-list h5").each(function() {
        $this = $(this).parent();
        //0未到，1到课
        studentArray.push({
            sid: $this.find(".student-id").val(),
            sign_type: $this.attr("data"),
            vendor_member_id:$this.find(".member-id").val()
        });
    });
    send.student_data = studentArray;
    $.post(url, send, function(result) {
        if (parseInt(result.status) == 0) {
            // console.time("a");
            showDialog(0, "签到修改成功");
            // console.timeEnd("a");
        } else {
            showDialog(1, "签到修改失败，请稍后再试！");
        }
        $btn.button('reset');
    });
});
