/*
签到选择
 */
var page = 1;
$(document).on("touchend", "#student-list div", function(event) {
    event.preventDefault();
    var $this = $(this),
        text = "",
        i = $this.index();
    if ($this.find("h6").text() !== "") {
        if ($this.hasClass("sign-choose")) {
            text = "到课"
        } else {
            text = "未到"
        }
        $this.toggleClass("sign-choose").find("h6").text(text);
    }
});
/*
签到
 */
$('#sign-btn').click(function() {
    showDialog(2);
    var send = {},
        studentObj = {},
        studentArray = [];
    send.class_id = classid;
    $("#student-list h5").each(function() {
        $this = $(this).parent();
        //0未到，1到课
        studentArray.push({
            student_id: $this.find(".student-id").val(),
            vendor_member_id: $this.find(".vendor-id").val(),
            sign_type: ($this.hasClass("sign-choose") ? 0 : 1)
        });
    });
    send.student_data = studentArray;
    $.post(signurl, send, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            var html = "";
            var $firstLi = $(".sign-history-list li:first");
            var liLength = $(".sign-history-list li").length;
            html += '<li class="list-group-item" data-id="';
            html += result.sign_id;
            html += '"><div class="container-fluid"><div class="row"><div class="col-xs-2 text-center num" data-num="';
            var signNum = (liLength == 0 ? 0 : parseInt($firstLi.find(".num").attr("data-num")));
            html += signNum + 1;
            html += '">第';
            html += signNum + 1;
            html += '次</div><div class="col-xs-6"><span>到课&nbsp;';
            html += result.arrived_num;
            html += '</span><span>未到&nbsp;<i>';
            html += result.miss_num;
            html += '</i></span><p>';
            html += result.sign_time;
            html += '</p></div><div class="col-xs-2 text-right"><a class="btn btn-default btn-xs sign-backed" href="javascript:">撤回</a></div><div class="col-xs-2 text-right"><a class="btn btn-default btn-xs sign-update" href="javascript:" data-id="';
            html +=result.sign_id;
            html +='">查看</a></div></div></div></li>';
            if (liLength == 0) {
                $("ul.sign-history-list").html(html);
            } else {
                $firstLi.find(".col-xs-2:gt(0)").remove().end().append("<div class='col-xs-4'></div>");
                $firstLi.before(html);
            }''
            showDialog(0);
        } else if (parseInt(result.status) == 2) {
            showDialog(1, "请勿频繁操作，请稍后再试！");
            $(".sign-choose").removeClass("sign-choose").find("h6").text("到课");
        } else {
            showDialog(1, "请稍后再试！");
            $(".sign-choose").removeClass("sign-choose").find("h6").text("到课");
        }
    });
});
/*撤销签到*/
$(document).on("click", ".sign-backed", function() {
    $("#sign-backed-dialog").modal({});
    signStatus = false;
});
$("#sign-backed-confirm").click(function() {
    $.post(signbackurl, {
        sign_id: $(".sign-history-list li:first-child").attr("data-id")
    }, function(result) {
        if (parseInt(result.status) == 0) {
            $(".sign-history-list li:first").remove();
            showDialog(0, "撤销成功");
        } else {
            hideDialog(1, "撤销失败")
        }
        $("#sign-backed-dialog").modal('hide');
    }, 'json');
});
$('#photo input').on('change', function(e) {
    new html5ImgCompress(e.target.files[0], {
        maxWidth: 600,
        maxHeight: 600,
        quality: 0.6,
        before: function(file) {
            console.log('压缩前...');
            // 这里一般是对file进行filter，例如用file.type.indexOf('image') > -1来检验是否是图片
            // 如果为非图片，则return false放弃压缩（不执行后续done、fail、complete），并相应提示
        },
        done: function(file, base64) {
            console.log('压缩成功...');
            // ajax和服务器通信上传base64图片等操作
            img = new Image()
            var size = (base64.length / 1333).toFixed(2);
            img.src = base64;
            img.onload = function() {
                $("#photo").prepend(img)
                $("#del-photo-btn").show();
            }
            console.log(size);
        },
        fail: function(file) {
            console.log('压缩失败...');
        },
        complete: function(file) {
            console.log('压缩完成...');
            $("#loading").modal('hide');
        },
        notSupport: function(file) {
            console.log('浏览器不支持！')
                // 不支持操作，例如PC在这里可以采用swfupload上传
        }
    });
});
$(document).on("click", "img", function(e) {
    e.preventDefault();
    var imgs = $("#comment img");
    for (var i = 0, urls = []; i < imgs.length; i++) {
        urls.push(imgs[i].src);
    }
    wx.previewImage({
        current: e.target.src,
        urls: urls
    });
});
$("#del-photo-btn").click(function() {
    $(this).hide();
    $("#photo img").remove();
});
$("#comment-text").keyup(function() {
    var txt = $.trim($(this).val());
    var txtL = txt.length;
    if (txtL > 200) {
        $(this).val(txt.substr(0, 50));
    };
});
$("#comment-btn").click(function() {
    var cont = $.trim($("#comment-text").val());
    if (cont.length < 2) {
        showDialog(1, "点评内容至少填写2个字", 2);
        return false;
    };
    var send = {};
    send.class_id = classid;
    send.content = $.trim($("#comment-text").val());
    send.file = $("#photo img").attr("src");
    showDialog(2);
    $.post(commenturl, send, function(result) {
        if (parseInt(result.status) == 0) {
            location.reload();
            showDialog(0);
        } else {
            showDialog(1);
        };
    });
});
// todo 获取历史评价
var commentStatus = true;
var signStatus = true;
$(function() {
    if (window.location.hash == "#sign") {
        console.log("切换到课堂签到");
        $("#sign_btn").click();
    } else if (window.location.hash == "#comment") {
        console.log("切换到课堂点评");
        $("#history_btn").click();
    } else {
        console.log("页面加载触发", signStatus);
        commentStatus = false;
        signStatus = false;
        getsignhistory();
    }
});
$("#history_btn").click(function() {
    console.log("click")
    window.location.hash = "#comment";
    signStatus = false;
    commentStatus = true;
    if ($("#history_comment a").length < 1) {
        console.log("点击触发历史", commentStatus);
        commentStatus = false
        getcommenthistory();
    }
});
$("#sign_btn").click(function() {
    window.location.hash = "#sign";
    commentStatus = false;
    signStatus = true;
    if ($(".sign-history-list li").length < 1) {
        console.log("点击触发", signStatus);
        signStatus = false;
        getsignhistory();
    }
});

function getcommenthistory() {
    console.log("去获取comment");
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(historyurl, {
        class_id: classid,
        p: page
    }, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            var html = "";
            $.each(result, function(i, history) {
                html += '<a class="list-group-item" href="'
                html += commentinfo + '&eval_id=' + history.geval_deta_id
                html += '"><div class="container-fluid"><div class="row comments-head"><div         class="col-xs-6">';
                html += history.geval_deta_time
                html += '</div><div class="col-xs-6 text-right"><span class="edit-bg">家长反馈</span>';
                if (history.read > 0) {
                    html += '<span class="comments-num">' + history.eval_num + '条</span>';
                } else {
                    html += '<span>' + history.eval_num + '条</span>';
                };
                html += '</div></div><div class="row comments-body"><div class="col-xs-12">' + history.geval_deta_text + '</div>';
                if (history.pic != null && history.pic.wxh5_pic != "") {
                    html += '<div class="col-xs-12"><img src="' + history.pic.wxh5_pic + '" alt=""></div>';
                };
                html += '</div></div></a>';
            });
            $("#history_comment").append(html);
            page++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多历史记录');
            }
        } else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史评价');
        }
        commentStatus = true;
    }, 'json')
};
// todo 历史签到
var signhistoryPage = 1;
function getsignhistory() {
    console.log("去获取sign");
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
    $.post(historysignurl, {
        class_id: classid,
        p: signhistoryPage
    }, function(result) {
        if (parseInt(result.status) == 0) {
            result = result.data;
            var html = "";
            $.each(result, function(i, history) {
                html += '<li class="list-group-item" data-id="';
                html += history.id;
                html += '"><div class="container-fluid"><div class="row"><div class="col-xs-2 text-center num" data-num="';
                html += history.num;
                html += '">第';
                html += history.num;
                html += '次</div><div class="col-xs-6"><span>到课&nbsp;';
                html += history.arrived_num;
                html += '</span><span>未到&nbsp;<i>';
                html += history.miss_num;
                html += '</i></span><p>';
                html += history.sign_time;
                html += '</p></div><div class="col-xs-4 text-right"><a class="btn btn-default btn-xs sign-update" href="javascript:" data-id="';
                html +=history.id;
                html +='">查看</a></div></div></div></li>';
            });
            $(".sign-history-list").append(html);
            $(".list-group .list-group-item:first-child .col-xs-4").replaceWith('<div class="col-xs-2 text-right"><a class="btn btn-default btn-xs sign-backed" href="javascript:">撤回</a></div><div class="col-xs-2 text-right"><a class="btn btn-default btn-xs sign-update" href="javascript:">查看</a></div>');
            var data_id_fir=$('.list-group .list-group-item:first-child').attr('data-id');
            $(".list-group .list-group-item:first-child .sign-update").attr('data-id',data_id_fir);
            signhistoryPage++;
            if (result.length < 10) {
                $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多历史记录');
            }
        } else {
            $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">对不起，没有更多历史记录');
        }
        signStatus = true;
    }, 'json')
}
$(document).on('click','.sign-update',function () {
    console.log("click")
   var data_id=$(this).attr("data-id");
    window.location.href=signediturl+'&sign_id='+data_id;
});
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h && $("body").height() == h) {
        if (commentStatus) {
            console.log("滚动comment", commentStatus)
            commentStatus = false;
            getcommenthistory();
        } else if (signStatus) {
            console.log(newH, h, $("body").height(), "滚动sign", signStatus)
            signStatus = false;
            getsignhistory();
        }
    }
});
