var msg_page=1;
var list_canscroll=false;
function show_list() {
    $(".loader").html('<div class="loader-inner line-scale-pulse-out"><div></div><div></div><div></div><div></div><div></div></div>');
   $.post(getmsgurl,{p:msg_page},function (result) {
       console.log(result)
       if(result.status==0){
           result=result.data;
           var html="";
           $.each(result, function(i, data) {
               html+='<a class="list-group-item angle-right-bg" href=';
               html+=msgediturl;
               html+='&mid=';
               html+=data.id;
               html+='><h5 class="over-text">';
               html+=data.message_name;
               html+='</h5><p class="over-text">';
               for(var j=0;j<data.class_name.length;j++){
                   console.log(data.class_name[j].cname);
                   html+='<span>'+data.class_name[j].cname+'</span>';
               }
               html+='</p><span>';
               html+=data.send_time;
               html+='</span><span class="send_note';
               if(data.is_send==1){
                   html+='">已发送';
               }else {
                   html+=' send_not">未发送';
               }
               html+='</span></a>';
           });
           $(".msg_list").append(html);
           msg_page++;
           if (result.length < 10) {
               $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多数据了');
           }
       }else{
           $(".loader").html('<img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多数据了')
       }
       list_canscroll=true;
       if($(".msg_list .list-group-item").length==0){
           $(".loader").hide();
           $(".msg_list").html('<div class="row"><div class="col-lg-4 col-lg-offset-4  col-sm-6 col-sm-offset-3 col-xs-8 col-xs-offset-2 text-right select-none" id="note_1"><img src="' + imgurl + 'Public/wechat/images/smaile.png">没有更多数据了</div></div>')
       }
   },'json');
}
show_list();
var h, newH;
$(window).scroll(function() {
    h = $(document).height();
    newH = $(window).scrollTop() + window.screen.availHeight;
    if (newH >= h &&($("body").height() >= h)&&list_canscroll) {
        list_canscroll=false;
        show_list();
    }
});



