$(".personal_icon").addClass("active");
var phone=$.trim($(".personal_content p").text());
$("#remove_bind").click(function () {
    if(phone!=""){
        showDialog(2);
        $.post(removeBindurl,{phone:phone},function (result) {
            if(parseInt(result.status)==0){
                showDialog(0);
                window.location.href=myShowurl;
            }else {
                showDialog(1,result.data);
            }
        },"json")
    }else {
        showDialog(1,"请稍后再试");
    }
})




